import { RectanglePositionStyle } from "../components/MicroflowObjects/MicroflowObject";
import Microflows from "../model/gen/Microflows.gen";
import { FlowsMap, MicroflowObjectMap, OutputVariableObjectMap } from "../types/microflow";

import { CanvasConstants } from "./CanvasConstants";
import { Rectangle } from "./Rectangle";

export type EmptyReference = "" | null | undefined;
export type MicroflowEventType =
    | Microflows.StartEvent
    | Microflows.ContinueEvent
    | Microflows.BreakEvent
    | Microflows.ErrorEvent
    | Microflows.EndEvent;
export type MicroflowObjectPositions = Record<"leftPositions" | "topPositions" | "rightPositions" | "bottomPositions", number[]>;

export function createMicroflowObjectMaps(
    collection: Microflows.MicroflowObjectCollection,
    microflowObjectMap: MicroflowObjectMap = new Map(),
    outputVariableObjectMap: OutputVariableObjectMap = new Map()
): [MicroflowObjectMap, OutputVariableObjectMap] {
    collection.objects.forEach(microflowObject => {
        switch (microflowObject.$Type) {
            case "Microflows$ActionActivity":
                if (isVariableSource(microflowObject.action) && microflowObject.action.outputVariableName) {
                    outputVariableObjectMap.set(microflowObject.action.outputVariableName, {
                        microflowObject,
                        collection: collection.$ID
                    });
                }

                break;
            case "Microflows$MicroflowParameterObject":
                outputVariableObjectMap.set(microflowObject.name, { microflowObject, collection: collection.$ID });
                break;
            case "Microflows$LoopedActivity":
                createMicroflowObjectMaps(microflowObject.objectCollection, microflowObjectMap, outputVariableObjectMap);
                break;
        }

        microflowObjectMap.set(microflowObject.$ID, { microflowObject, collection: collection.$ID });
    });

    return [microflowObjectMap, outputVariableObjectMap];
}

export function createFlowsMap(microflowObjectMap: MicroflowObjectMap, flows: Microflows.Flow[]): FlowsMap {
    const flowsMap: FlowsMap = new Map();

    flows.forEach(flow => {
        const originDetails = microflowObjectMap.get(flow.origin);

        const destinationDetails = microflowObjectMap.get(flow.destination);

        if (!originDetails || !destinationDetails) {
            throw new Error("Flow without origin/destination alert");
        }

        if (!flowsMap.has(originDetails.collection)) flowsMap.set(originDetails.collection, []);

        flowsMap
            .get(originDetails.collection)
            ?.push({ flow, origin: originDetails.microflowObject, destination: destinationDetails.microflowObject });
    });

    return flowsMap;
}

export function getRectangle(microflowObject: Microflows.MicroflowObject): Rectangle {
    const { height, width } = microflowObject.size;
    const { x: left, y: top } = microflowObject.relativeMiddlePoint;

    return new Rectangle(left - width / 2, top - height / 2, width, height);
}

export function getPositionStyleForRectangle(rectangle: Rectangle): RectanglePositionStyle {
    return {
        top: `${rectangle.top}px`,
        left: `${rectangle.left}px`,
        width: `${rectangle.width}px`,
        height: `${rectangle.height}px`
    };
}

export function getPositionStyleForMicroflowObject(microflowObject: Microflows.MicroflowObject): RectanglePositionStyle {
    return getPositionStyleForRectangle(getRectangle(microflowObject));
}

export function findSourceVariableEntity(
    microflowObject: Microflows.MicroflowParameterObject | Microflows.ActionActivity
): string | undefined {
    if (microflowObject.$Type === "Microflows$MicroflowParameterObject") {
        return microflowObject.variableType.$Type === "DataTypes$ListType" || microflowObject.variableType.$Type === "DataTypes$ObjectType"
            ? microflowObject.variableType.entity
            : undefined;
    }

    if (microflowObject.action && microflowObject.action.$Type === "Microflows$RetrieveAction") {
        const retrieveSource = microflowObject.action.retrieveSource;

        if (!retrieveSource) {
            return undefined;
        }

        if (retrieveSource.$Type === "Microflows$DatabaseRetrieveSource") {
            return retrieveSource.entity || undefined;
        }

        if (retrieveSource.$Type === "Microflows$AssociationRetrieveSource") {
            // TODO: retrieve association to get entity of other end of the association. Don't forget to implement the unit test.
            return undefined;
        }
    }

    return undefined;
}

export function getMicroflowObjectPositions(microflowObjects: Microflows.MicroflowObject[]): MicroflowObjectPositions {
    const leftPositions: number[] = [];
    const topPositions: number[] = [];
    const rightPositions: number[] = [];
    const bottomPositions: number[] = [];

    microflowObjects.forEach(object => {
        const rectangle = getRectangle(object);

        leftPositions.push(rectangle.left - CanvasConstants.CANVAS_PADDING);
        topPositions.push(rectangle.top - CanvasConstants.CANVAS_PADDING);
        rightPositions.push(rectangle.right + CanvasConstants.CANVAS_PADDING);
        bottomPositions.push(rectangle.bottom + CanvasConstants.CANVAS_PADDING);
    });

    return { leftPositions, topPositions, rightPositions, bottomPositions };
}

export function isVariableSource(
    action: Microflows.MicroflowAction | EmptyReference
): action is Microflows.MicroflowAction & { outputVariableName: string } {
    if (!action) {
        return false;
    }

    // eslint-disable-next-line no-prototype-builtins
    return action.hasOwnProperty("outputVariableName");
}
