import { computed } from "mobx";
import { observer } from "mobx-react";
import { FC, useContext } from "react";
import styled from "styled-components";

import { Rectangle } from "../../../helpers/Rectangle";
import { getDataTypeInfo } from "../../../helpers/dataTypeHelpers";
import {
    findSourceVariableEntity,
    getPositionStyleForMicroflowObject,
    getPositionStyleForRectangle
} from "../../../helpers/microflowHelpers";
import Microflows from "../../../model/gen/Microflows.gen";
import { StateContext } from "../../../state/AppState";
import { ParameterShape } from "../../shapes/ParameterShape";
import { ObjectCollection } from "../MicroflowContainer";
import { MicroflowObject } from "../MicroflowObject";

import { WhileLoopIndicator } from "./WhileLoopIndicator";

export const LoopedActivity: FC<{ microflowObject: Microflows.LoopedActivity }> = observer(({ microflowObject }) => {
    const state = useContext(StateContext);
    const style = getPositionStyleForMicroflowObject(microflowObject);

    const parameterContainerStyle = getPositionStyleForRectangle(
        new Rectangle(30, 20, microflowObject.loopSource.$Type === "Microflows$IterableList" ? 30 : 200, 30)
    );

    const outputVariable = computed(() => {
        if (microflowObject.loopSource.$Type === "Microflows$WhileLoopCondition") {
            return;
        }

        const sourceVariableDetails = state.outputVariableObjectMap?.get(microflowObject.loopSource.listVariableName);

        const sourceVariableEntity = sourceVariableDetails ? findSourceVariableEntity(sourceVariableDetails.microflowObject) : undefined;

        return {
            name: microflowObject.loopSource.variableName,
            type: sourceVariableEntity
                ? getDataTypeInfo("DataTypes$ObjectType", sourceVariableEntity)
                : getDataTypeInfo("DataTypes$UnknownType")
        };
    }).get();

    return (
        <StyledLoopedActivity {...style} data-testid="looped-activity">
            <ObjectCollection collection={microflowObject.objectCollection} />
            <MicroflowObject {...parameterContainerStyle}>
                {microflowObject.loopSource.$Type === "Microflows$IterableList" ? (
                    <ParameterShape isIterator outputVariable={outputVariable} />
                ) : (
                    <WhileLoopIndicator caption={microflowObject.loopSource.caption} />
                )}
            </MicroflowObject>
        </StyledLoopedActivity>
    );
});

const StyledLoopedActivity = styled(MicroflowObject)`
    border: solid 2px #6ebdff;
    border-radius: 10px;
    background-color: #dceefe;
`;
