export type MessageHandler = (message: string, data: unknown[]) => boolean | Promise<boolean>;

export type MessageBus = {
    addMessageHandler(handler: MessageHandler): void;
    removeMessageHandler(handler: MessageHandler): void;
    postMessage(message: string, data?: Record<string, unknown>): void;
};

export * as Argument from "./argument";

/**
 * Try getting a message bus implementation. If no implementation is available on the window, this method will return
 * a stub when `isDevMode` is `true`, and throws an error if `isDevMode` is `false`.
 */
export function getMessageBus(isDevMode: boolean = false): MessageBus {
    if (window.chrome?.webview) {
        return new WebViewMessageBus(window.chrome.webview);
    } else if (window.webkit?.messageHandlers.studioPro) {
        return new WebKitMessageBus(window.webkit.messageHandlers.studioPro);
    } else if (isDevMode) {
        return new StubMessageBus();
    }
    throw new Error("Cannot use WEF Transportation in a stand-alone browser.");
}

function isObject(target: unknown): target is Record<string, unknown> {
    return typeof target === "object" && !!target;
}

abstract class BaseMessageBus implements MessageBus {
    readonly handlers: MessageHandler[] = [];

    protected async handleMessage(message: string, data: unknown[]) {
        let last_error = null;

        for (let handler of this.handlers) {
            try {
                if (await handler(message, data)) return;
            } catch (e) {
                last_error = e;
                console.error("Error in message handler", e);
            }
        }

        if (last_error) {
            // TODO: Send details to Studio Pro?
            // TODO: Do we want a global error handler utility here?
            return;
        }

        console.error("No message handler for message", message, data);
        // Send error message back that there was no handler for debugging
        this.postMessage("MessageListenerError", { message });
    }

    addMessageHandler(handler: MessageHandler) {
        this.handlers.push(handler);
        this.postMessage("MessageListenerRegistered");
    }

    removeMessageHandler(handler: MessageHandler) {
        const index = this.handlers.indexOf(handler);
        if (index !== -1) {
            this.handlers.splice(index, 1);
        }
    }

    abstract postMessage(message: string, data?: Record<string, unknown>): void;
}

class WebViewMessageBus extends BaseMessageBus {
    constructor(readonly messageBus: IWebView2MessageBus) {
        super();

        messageBus.addEventListener("message", ev => {
            if (!isObject(ev.data)) return;
            const message = ev.data["message"];
            const data = ev.data["data"];
            if (typeof message === "string" && Array.isArray(data)) {
                void this.handleMessage(message, data);
            }
        });
    }

    postMessage(message: string, data?: Record<string, unknown>) {
        this.messageBus.postMessage({ message, data });
    }
}

class WebKitMessageBus extends BaseMessageBus {
    constructor(readonly messageBus: IWebKitMessageBus) {
        super();

        // Since there is no easy way to handle this with WKWebView (it's all theoretical anyway..)
        (window as any).WKPostMessage = (json: string) => {
            // To avoid issues serializing data, we JSONify the data when posting.
            const wkMessage = JSON.parse(json);
            const message = wkMessage["message"];
            const data = wkMessage["data"];
            if (typeof message === "string" && Array.isArray(data)) {
                void this.handleMessage(message, data);
            }
        };
    }

    postMessage(message: string, data?: Record<string, unknown>) {
        this.messageBus.postMessage(JSON.stringify({ message, data }));
    }
}

class StubMessageBus implements MessageBus {
    addMessageHandler(handler: MessageHandler) {
        console.log("Added handler", handler);
    }

    removeMessageHandler(handler: MessageHandler) {
        console.log("Removed handler", handler);
    }

    postMessage(message: string, data: Record<string, unknown>) {
        console.log("Post message", message, data);
    }
}
