import { action, computed, observable } from "mobx";

import { CanvasConstants } from "../helpers/CanvasConstants";
import { CanvasViewerEventHandler, CanvasViewerEventHandlerCallbacks } from "../helpers/CanvasViewerEventHandler";
import { Store } from "../helpers/mobxHelpers";

export type CanvasScroll = Record<"top" | "left", number>;
export type CanvasViewerBounds = Record<"x" | "y" | "width" | "height", number>;

export type MousePointerLocation = Record<"x" | "y", number>;

@Store
export class CanvasViewManager {
    @observable public zoomLevel: number = CanvasConstants.DEFAULT_ZOOM_LEVEL;
    @observable public canvasScroll: CanvasScroll = { left: 0, top: 0 };
    @observable public canvasViewerBounds: CanvasViewerBounds | null = null;

    private canvasViewerEventHandler: CanvasViewerEventHandler | null = null;

    @computed
    public get canvasViewportRect() {
        if (!this.canvasViewerBounds) return { x: 0, y: 0, width: 0, height: 0 };
        const scale = 100 / this.zoomLevel;

        return {
            x: -(this.canvasScroll.left * scale),
            y: -(this.canvasScroll.top * scale),
            width: this.canvasViewerBounds.width * scale,
            height: this.canvasViewerBounds.height * scale
        };
    }

    public disposeCanvasViewer = () => {
        this.canvasViewerEventHandler?.dispose();
        this.canvasViewerEventHandler = null;
    };

    @action
    public setCanvasRef = (node: HTMLElement | null) => {
        this.disposeCanvasViewer();

        if (node) {
            const { x, y, width, height } = node.getBoundingClientRect();

            this.canvasViewerBounds = { x, y, width, height };
            this.canvasViewerEventHandler = new CanvasViewerEventHandler(
                node,
                this.scrollBy,
                this.zoomIn,
                this.zoomOut,
                this.zoomBy,
                this.resetZoom
            );
        } else {
            this.canvasViewerBounds = null;
        }
    };

    @action
    public scrollBy: CanvasViewerEventHandlerCallbacks["scrollBy"] = (dx: number, dy: number) => {
        this.canvasScroll.left += dx;
        this.canvasScroll.top += dy;
    };

    @action
    public scrollTo = (left: number, top: number) => {
        this.canvasScroll = { left, top };
    };

    public zoomIn: CanvasViewerEventHandlerCallbacks["zoomIn"] = (mousePointerLocation?: MousePointerLocation) => {
        this.zoomTo(this.zoomLevel + CanvasConstants.ZOOM_STEP, mousePointerLocation);
    };

    public zoomOut: CanvasViewerEventHandlerCallbacks["zoomOut"] = mousePointerLocation => {
        this.zoomTo(this.zoomLevel - CanvasConstants.ZOOM_STEP, mousePointerLocation);
    };

    public zoomBy: CanvasViewerEventHandlerCallbacks["zoomBy"] = (deltaZ: number, mousePointerLocation?: MousePointerLocation) => {
        this.zoomTo(this.zoomLevel + deltaZ, mousePointerLocation);
    };

    public resetZoom: CanvasViewerEventHandlerCallbacks["resetZoom"] = () => {
        this.zoomTo(CanvasConstants.DEFAULT_ZOOM_LEVEL);
    };

    @action
    public zoomTo = (zoomLevel: number, mousePointerLocation?: { x: number; y: number }) => {
        const newZoom = clamp(zoomLevel, CanvasConstants.MIN_ZOOM, CanvasConstants.MAX_ZOOM);

        if (this.canvasViewerBounds) {
            if (!mousePointerLocation) {
                mousePointerLocation = { x: this.canvasViewerBounds.width / 2, y: this.canvasViewerBounds.height / 2 };
            }

            // When a mousePointerLocation is passed, we need to adjust the viewport offset to make scrolling *somewhat* use that origin point
            // E.g. when zooming in the canvas location that was under the mouse, should afterwards still be under the mouse.
            // When zooming, a canvas location shifts by its distance from 0,0 times the zoom amount

            const xCoordinatePointerOnCanvas = mousePointerLocation.x - this.canvasViewerBounds.x;
            const yCoordinatePointerOnCanvas = mousePointerLocation.y - this.canvasViewerBounds.y;

            // Current point in zoomed-in world
            const originPointZoomedWorld = {
                x: xCoordinatePointerOnCanvas - this.canvasScroll.left,
                y: yCoordinatePointerOnCanvas - this.canvasScroll.top
            };

            // Current point in the canvas world (or, 100%)
            const originPointWorld = {
                x: originPointZoomedWorld.x / (this.zoomLevel / 100),
                y: originPointZoomedWorld.y / (this.zoomLevel / 100)
            };

            // New point in zoomed-in world after zooming
            const newOriginPointZoomedWorld = { x: originPointWorld.x * (newZoom / 100), y: originPointWorld.y * (newZoom / 100) };
            // Get difference in zoomed-in worlds
            const dx = newOriginPointZoomedWorld.x - originPointZoomedWorld.x;
            const dy = newOriginPointZoomedWorld.y - originPointZoomedWorld.y;

            this.canvasScroll.left -= dx;
            this.canvasScroll.top -= dy;
        }

        this.zoomLevel = newZoom;
    };
}

export function clamp(value: number, min: number, max: number): number {
    if (value < min) return min;
    if (value > max) return max;

    return value;
}
