import { FC } from "react";
import styled from "styled-components";

import { getPositionStyleForMicroflowObject } from "../../helpers/microflowHelpers";
import Microflows from "../../model/gen/Microflows.gen";

export const Annotation: FC<{ microflowObject: Microflows.Annotation }> = ({ microflowObject: annotation }) => {
    const style = getPositionStyleForMicroflowObject(annotation);

    return (
        <Box style={{ position: "absolute", ...style }}>
            <Handle />
            {annotation.caption}
        </Box>
    );
};

const Box = styled.div`
    background-color: #d9dbdd;
    color: black;

    font-size: 10px;
    padding: 10px;

    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const Handle = styled.div`
    // Do not render in the Box's flex layout
    position: absolute;

    top: 0;
    left: 0;

    margin: -2px;
    height: calc(100% + 4px);
    width: 52px;

    border: 2px solid #298efb;
    border-width: 2px 0px 2px 2px;

    pointer-events: none;
`;
