import { FC, useContext } from "react";

import { assertNever } from "../../helpers/assertNever";
import Microflows from "../../model/gen/Microflows.gen";
import { StateContext } from "../../state/AppState";
import { ActiveProblemsBadges } from "../ActiveProblemsBadges";

import { ActionActivity } from "./ActionActivity";
import { Annotation } from "./Annotation";
import { ExclusiveMerge } from "./ExclusiveMerge";
import { ExclusiveSplit } from "./ExclusiveSplit";
import { InheritanceSplit } from "./InheritanceSplit";
import { LoopedActivity } from "./LoopedActivity/LoopedActivity";
import { MicroflowEvent } from "./MicroflowEvent";
import { Parameter } from "./Parameter";
import { AnnotationFlow } from "./flows/AnnotationFlow";
import { SequenceFlow } from "./flows/SequenceFlow";

export const ObjectCollection: FC<{ collection: Microflows.MicroflowObjectCollection }> = ({ collection }) => {
    const state = useContext(StateContext);

    const flows = state.flowsMap?.get(collection.$ID) ?? [];

    return (
        <>
            {flows.map(renderFlow)}
            {collection.objects.map(renderMicroflowObject)}
        </>
    );
};

const renderMicroflowObject = (microflowObject: Microflows.MicroflowObject) => {
    return (
        <>
            {renderMicroflowObjectByType(microflowObject)}
            <ActiveProblemsBadges microflowObject={microflowObject} />
        </>
    );
};

function renderFlow({ flow }: { flow: Microflows.Flow }): JSX.Element {
    switch (flow.$Type) {
        case "Microflows$SequenceFlow":
            return <SequenceFlow flow={flow} key={flow.$ID} />;
        case "Microflows$AnnotationFlow":
            return <AnnotationFlow flow={flow} key={flow.$ID} />;
        default:
            assertNever(flow);
    }
}

function renderMicroflowObjectByType(microflowObject: Microflows.MicroflowObject): JSX.Element {
    switch (microflowObject.$Type) {
        case "Microflows$StartEvent":
        case "Microflows$ContinueEvent":
        case "Microflows$EndEvent":
        case "Microflows$BreakEvent":
        case "Microflows$ErrorEvent":
            return <MicroflowEvent key={microflowObject.$ID} microflowObject={microflowObject} />;
        case "Microflows$MicroflowParameterObject":
            return <Parameter key={microflowObject.$ID} microflowObject={microflowObject} />;
        case "Microflows$LoopedActivity":
            return <LoopedActivity key={microflowObject.$ID} microflowObject={microflowObject} />;
        case "Microflows$InheritanceSplit":
            return <InheritanceSplit key={microflowObject.$ID} microflowObject={microflowObject} />;
        case "Microflows$ExclusiveSplit":
            return <ExclusiveSplit key={microflowObject.$ID} microflowObject={microflowObject} />;
        case "Microflows$ExclusiveMerge":
            return <ExclusiveMerge key={microflowObject.$ID} microflowObject={microflowObject} />;
        case "Microflows$ActionActivity":
            return <ActionActivity key={microflowObject.$ID} microflowObject={microflowObject} />;
        case "Microflows$Annotation":
            return <Annotation key={microflowObject.$ID} microflowObject={microflowObject} />;
        default:
            assertNever(microflowObject);
    }
}
