import { runInAction } from "mobx";

import Microflows from "../model/gen/Microflows.gen";
import { Breakpoint, IState } from "../state/IState";
import { CheckSeverity, Problem, ProblemLocation } from "../types/problems";

export function getAllObjectIds(
    objects: Microflows.MicroflowObject[],
    filter?: (microflowObject: Microflows.MicroflowObject) => boolean
): string[] {
    const objectsIDs: string[] = [];

    for (const object of objects) {
        if (!filter || (filter && filter(object))) {
            objectsIDs.push(object.$ID);
        }

        if (object.$Type === "Microflows$LoopedActivity") {
            objectsIDs.push(...getAllObjectIds(object.objectCollection.objects, filter));
        }
    }

    return objectsIDs;
}

export function generateMockProblems(state: IState) {
    if (!state.openDocument) return;

    const allObjectIds = getAllObjectIds(state.openDocument.objectCollection.objects);

    const problemLocations: ProblemLocation[] = allObjectIds.map(elementId => ({
        elementId,
        unitId: "",
        module: "",
        document: "",
        element: ""
    }));

    const checkSeverities: CheckSeverity[] = ["Error", "Warning"];

    const activeProblems: Problem[] = checkSeverities.map(severity => ({
        name: null,
        locations: problemLocations,
        message: "",
        severity,
        errorCode: null,
        documentationUrl: null,
        arguments: null,
        studioMessage: null,
        studioDocumentationUrl: null
    }));

    state.setActiveProblems(activeProblems);
}

export function generateMockBreakpoints(state: IState) {
    if (!state.openDocument) return;

    // Source of the list: Mendix.Modeler.Debugger/Tasks/BreakpointTaskRegistry.cs
    const availableTypes: Microflows.MicroflowObject["$Type"][] = [
        "Microflows$ActionActivity",
        "Microflows$EndEvent",
        "Microflows$ErrorEvent",
        "Microflows$ExclusiveSplit",
        "Microflows$InheritanceSplit"
    ];

    const typeFilter = (microflowObject: Microflows.MicroflowObject) => availableTypes.includes(microflowObject.$Type);

    const microflowObjectIds = getAllObjectIds(state.openDocument.objectCollection.objects, typeFilter);

    const breakpoints: Breakpoint[] = microflowObjectIds.map(ContainerID => ({
        ContainerID,
        Enabled: Math.random() > 0.5
    }));

    runInAction(() => {
        state.breakpoints = breakpoints;
    });
}
