import { action, computed, observable } from "mobx";

import { getMessageBus, MessageBus } from "../../../transportation/src";
import { CanvasBounds, getCanvasBounds } from "../helpers/canvasHelpers";
import {
    createFlowsMap,
    createMicroflowObjectMaps,
    getMicroflowObjectPositions,
    MicroflowObjectPositions
} from "../helpers/microflowHelpers";
import { Store } from "../helpers/mobxHelpers";
import { CreateChangeCommit } from "../model/CreateChangeCommit";
import { Microflow1 } from "../model/Microflow1";
import { Microflow2 } from "../model/Microflow2";
import { MicroflowAllTheActions } from "../model/MicroflowAllTheActions";
import { MicroflowWithParameters } from "../model/MicroflowWithParameters";
import $Primitives from "../model/gen/$Primitives.gen";
import Microflows from "../model/gen/Microflows.gen";
import { FlowsMap, MicroflowObjectMap, OutputVariableObjectMap } from "../types/microflow";
import { Problem } from "../types/problems";

import { CanvasViewManager } from "./CanvasViewManager";
import { Breakpoint, IState } from "./IState";

@Store
export class State implements IState {
    @observable
    private microflow?: Microflows.Microflow;

    @observable
    microflowObjectMap?: MicroflowObjectMap;

    @observable
    outputVariableObjectMap?: OutputVariableObjectMap;

    @observable
    flowsMap?: FlowsMap;

    @observable
    canvasViewManager: CanvasViewManager = new CanvasViewManager();

    availableDocuments: Microflows.Microflow[] = [
        Microflow1,
        Microflow2,
        MicroflowWithParameters,
        MicroflowAllTheActions,
        CreateChangeCommit
    ];

    @observable
    activeProblems: Problem[] = [];

    @observable
    errorCounts: Record<string, number> = {};

    @observable
    warningCounts: Record<string, number> = {};

    @observable
    breakpoints: Breakpoint[] = [];

    readonly messageBus: MessageBus;

    readonly isDevMode: boolean;

    constructor({ isDevMode }: { isDevMode: boolean }) {
        this.messageBus = getMessageBus(isDevMode);

        this.isDevMode = isDevMode;

        this.initializeMessageBus();
    }

    @computed
    get canvasBounds(): CanvasBounds | null {
        const microflowObjectPositions = this.microflowObjectPositions;

        if (!microflowObjectPositions) return null;

        return getCanvasBounds(this, microflowObjectPositions);
    }

    @computed
    private get microflowObjectPositions(): MicroflowObjectPositions | null {
        if (!this.microflow) return null;

        return getMicroflowObjectPositions(this.microflow.objectCollection.objects);
    }

    @computed
    get openDocument() {
        return this.microflow;
    }

    set openDocument(document: Microflows.Microflow | undefined) {
        this.microflow = document;

        if (!this.microflow) {
            this.microflowObjectMap = undefined;
            this.outputVariableObjectMap = undefined;
            this.flowsMap = undefined;
            return;
        }

        const [microflowObjectMap, outputVariableObjectMap] = createMicroflowObjectMaps(this.microflow.objectCollection);

        this.microflowObjectMap = microflowObjectMap;
        this.outputVariableObjectMap = outputVariableObjectMap;
        this.flowsMap = createFlowsMap(this.microflowObjectMap, this.microflow.flows);

        this.storeDocumentId(this.microflow);

        if (this.canvasBounds) {
            this.canvasViewManager.scrollTo(0 - this.canvasBounds.x, 0 - this.canvasBounds.y);
        }
    }

    private storeDocumentId(document: Microflows.Microflow) {
        localStorage.setItem("openDocumentId", document.$ID);
    }

    openDevModeDocument() {
        if (!this.isDevMode) {
            return;
        }

        const storedDocumentId = localStorage.getItem("openDocumentId");

        if (storedDocumentId) {
            this.openDocument = this.availableDocuments.find(document => document.$ID === storedDocumentId);
        } else {
            this.openDocument = this.availableDocuments[0];
        }
    }

    private initializeMessageBus() {
        this.messageBus.addMessageHandler((command, data) => {
            if (command === "openMicroflow") {
                const microflowId = data[0] as string;

                fetch(`./api/microflow/${microflowId}`)
                    .then(response => response.json())
                    .then((json: $Primitives.$Element[]) => {
                        const microflow = json.find(el => el.$ID === microflowId && el.$Type === "Microflows$Microflow");

                        if (microflow) {
                            this.openDocument = microflow as Microflows.Microflow;
                        }
                    });

                fetch(`./api/breakpoints/${microflowId}`)
                    .then(response => response.json())
                    .then((breakpoints: Breakpoint[]) => {
                        this.breakpoints = breakpoints;
                    });
            } else if (command === "pushProblems") {
                const activeProblems: Problem[] = JSON.parse(data[0] as string);

                this.setActiveProblems(activeProblems);
            } else {
                // eslint-disable-next-line no-console
                console.warn(`Unknown request type ${command}`);
            }

            return true;
        });
    }

    @action
    public setActiveProblems = (activeProblems: Problem[]) => {
        this.activeProblems = activeProblems;

        this.errorCounts = {};
        this.warningCounts = {};

        for (const activeProblem of activeProblems) {
            for (const location of activeProblem.locations) {
                if (activeProblem.severity === "Error") {
                    if (!this.errorCounts[location.elementId]) {
                        this.errorCounts[location.elementId] = 0;
                    }

                    this.errorCounts[location.elementId] += 1;
                } else if (activeProblem.severity === "Warning") {
                    if (!this.warningCounts[location.elementId]) {
                        this.warningCounts[location.elementId] = 0;
                    }

                    this.warningCounts[location.elementId] += 1;
                }
            }
        }
    };
}
