import { BezierCurve } from "./../model/BezierCurve";
import { getDegreesFromXYPosition, getDiagonalDistanceBetweenPoints } from "./rectangleHelpers";

/**
 * Find the orientation that the arrow at the endPoint of the curve should have.
 *
 * @curve The Bézier curve in question
 * @arrowLength The length of the arrow that we want to draw.
 */
export function calculateFlowArrowOrientation(curve: BezierCurve, arrowLength: number) {
    const tForTailPoint = approximateTForArrow(curve, arrowLength);

    /** The point on the canvas where the back of the error will be */
    const arrowTailPoint = curve.coordinatesFor(tForTailPoint);

    /**
     * The line from the tail of the arrow to the tip,
     * translated to (0, 0) so we can measure the angle.
     */
    const arrowVector = {
        x: curve.end.x - arrowTailPoint.x,
        y: curve.end.y - arrowTailPoint.y
    };

    return getDegreesFromXYPosition(arrowVector.x, arrowVector.y);
}

/**
 * Find a good-enough value of `t` for @curve so that planar distance between
 * the point at that `t` and the endpoint matches @targetDistance.
 */
function approximateTForArrow(curve: BezierCurve, targetDistance: number): number {
    const minGuess = 0.7;
    const maxGuess = 0.95;

    /**
     * The best difference we found so far.
     *
     * Having an initial value of `Infinity` makes it easy to specify that any
     * first guess is better, since `x < Infinity` for any value of x.
     */
    let bestDifference = Infinity;

    /** The best value for `t` found so far. */
    let bestGuess = minGuess;

    for (let guess = maxGuess; guess > minGuess; guess -= 0.05) {
        const pointOnCanvas = curve.coordinatesFor(guess);
        const distance = getDiagonalDistanceBetweenPoints(pointOnCanvas, curve.end);

        const difference = Math.abs(distance - targetDistance);

        if (difference < bestDifference) {
            bestDifference = difference;
            bestGuess = guess;
        }
    }

    return bestGuess;
}
