import { FC } from "react";
import styled from "styled-components";

import iterateIconUrl from "../../assets/loopIcons/iterate.svg";
import { DataTypeInfo } from "../../helpers/dataTypeHelpers";

import { VariableOutputDescription } from "./VariableOutputDescription";

type OutputVariable = { name: string; type: DataTypeInfo };

export type ParameterProps = {
    isIterator?: boolean;
    outputVariable?: OutputVariable;
};

export const ParameterShape: FC<ParameterProps> = ({ isIterator, outputVariable }) => (
    <div data-testid={"parameter"}>
        <svg viewBox="0 0 30 30">
            <polygon points="1,1 14.6,1 28.6,15 14.6,29 1,29" fill="#FFF2CF" />
            <polygon points="1,1 14,1 29,14 14,29,1,29" stroke="#FFD45D" strokeWidth="1" fill="none" />
        </svg>
        {isIterator && <IterationIcon src={iterateIconUrl} alt="iterate" />}
        {outputVariable && <VariableOutputDescription name={outputVariable.name} type={outputVariable.type} />}
    </div>
);

const IterationIcon = styled.img`
    position: absolute;
    top: 7px;
    left: 3px;
    width: 17px;
    height: 15px;
`;
