import { FC, useContext } from "react";

import { BezierCurve } from "../../../model/BezierCurve";
import Microflows from "../../../model/gen/Microflows.gen";
import { StateContext } from "../../../state/AppState";

import { BezierHandle } from "./BezierHandle";
import { FlowCurve } from "./FlowCurve";
import { FlowLabel } from "./FlowLabel";

type Props = {
    flow: Microflows.SequenceFlow;
    showHandles?: boolean;
};

export const SequenceFlow: FC<Props> = ({ flow, showHandles }: Props) => {
    const state = useContext(StateContext);

    if (!state.microflowObjectMap) return null;
    if (!flow.origin || !flow.destination) return null;

    const origin = state.microflowObjectMap.get(flow.origin);
    const destination = state.microflowObjectMap.get(flow.destination);

    if (!origin || !destination) return null;

    const curve = BezierCurve.fromModel(flow, origin.microflowObject, destination.microflowObject);

    return (
        <>
            <FlowCurve objectId={flow.$ID} curve={curve}>
                {showHandles && (
                    <>
                        <BezierHandle point={curve.start} size={flow.originBezierVector} />
                        <BezierHandle point={curve.end} size={flow.destinationBezierVector} />
                    </>
                )}
            </FlowCurve>
            {flow.caseValue.$Type !== "Microflows$NoCase" && flow.caseValue.value ? (
                <FlowLabel curve={curve} label={flow.caseValue.value} />
            ) : null}
        </>
    );
};
