import { FC } from "react";
import styled from "styled-components";

import { BezierCurve } from "../../../model/BezierCurve";

const MAX_LABEL_WIDTH = 200;
const MAX_LABEL_HEIGHT = 24;

type Props = {
    curve: BezierCurve;
    label: string;
};

export const FlowLabel: FC<Props> = ({ curve, label }) => {
    const middlePoint = curve.coordinatesFor(0.5);

    const positionAndSize = {
        left: middlePoint.x - MAX_LABEL_WIDTH / 2,
        top: middlePoint.y - MAX_LABEL_HEIGHT / 2,
        width: MAX_LABEL_WIDTH,
        height: MAX_LABEL_HEIGHT
    };

    return (
        <LabelContainer style={positionAndSize}>
            <Label>{label}</Label>
        </LabelContainer>
    );
};

const LabelContainer = styled.div`
    position: absolute;

    text-align: center;

    /* Don't let this element interfere with anything */
    pointer-events: none;
`;

/*
 * NOTE: This is now a span because it's easier to correctly draw
 * the rectangle around the text with symmetric padding.
 *
 * It could become an svg text+rect, but we would need to make the
 * calculations really precise.
 */
const Label = styled.span`
    /* 'block' to make it respect the max-width property (otherwise it will just overflow)
     * 'inline' because otherwise it will expand horizontally and fill the whole LabelContainer
     */
    display: inline-block;

    font-size: 10px;
    background-color: white;
    border: 1px solid black;
    padding: 2px 2px;

    max-width: 100%;
    overflow-x: hidden;
    text-overflow: ellipsis;

    pointer-events: all;
`;
