import { observer } from "mobx-react";
import { FC, useCallback, useContext } from "react";
import styled from "styled-components";

import { StateContext } from "../state/AppState";

export const DocumentSelector: FC = observer(() => {
    const state = useContext(StateContext);

    const selectDocument = useCallback(
        (event: React.ChangeEvent<HTMLSelectElement>) => {
            const id = event.target.value;
            const document = id && state.availableDocuments.find(document => document.$ID === id);

            if (document) state.openDocument = document;
        },
        [state]
    );

    const id = state.openDocument?.$ID ?? "";

    return (
        <label>
            Microflow:&nbsp;
            <StyledSelect defaultValue={id} onChange={selectDocument}>
                {!id && <option value={""} />}
                {state.availableDocuments.map(document => (
                    <option key={document.$ID} value={document.$ID}>
                        {document.name}
                    </option>
                ))}
            </StyledSelect>
        </label>
    );
});

const StyledSelect = styled.select`
    width: 200px;
    height: 25px;
`;
