import { computed } from "mobx";
import { observer } from "mobx-react";
import { FC, CSSProperties, useContext } from "react";
import styled from "styled-components";

import { StateContext } from "../state/AppState";

import { ObjectCollection } from "./MicroflowObjects/MicroflowContainer";
import { ScrollBar } from "./ScrollBar";
import { TopBar } from "./TopBar";

export const MicroflowEditor: FC = observer(() => {
    const state = useContext(StateContext);

    const canvasStyle = computed<CSSProperties>(() => ({
        transform: `translate(${state.canvasViewManager.canvasScroll.left}px, ${state.canvasViewManager.canvasScroll.top}px) scale(${
            state.canvasViewManager.zoomLevel / 100
        })`
    })).get();

    return (
        <Container>
            <TopBar />
            <CanvasViewer ref={state.canvasViewManager.setCanvasRef} data-testid="canvas-viewer">
                {state.openDocument && (
                    <MicroflowWrapper style={canvasStyle}>
                        <ObjectCollection collection={state.openDocument.objectCollection} />
                    </MicroflowWrapper>
                )}
                <ScrollBar direction="vertical" />
                <ScrollBar direction="horizontal" />
            </CanvasViewer>
        </Container>
    );
});

const Container = styled.div`
    display: flex;
    flex-direction: column;
    height: 100vh;
`;

const CanvasViewer = styled.div`
    position: relative;
    flex: 1;
    overflow: hidden;
    user-select: none;
`;

const MicroflowWrapper = styled.div`
    position: absolute;
    transform-origin: 0 0;
`;
