import { clamp, MousePointerLocation } from "../state/CanvasViewManager";

import { CanvasConstants } from "./CanvasConstants";

export interface CanvasViewerEventHandlerCallbacks {
    scrollBy(dx: number, dy: number): void;
    zoomIn(mousePointerLocation?: MousePointerLocation): void;
    zoomOut(mousePointerLocation?: MousePointerLocation): void;
    zoomBy(zoomLevel: number, locamousePointerLocationtion?: MousePointerLocation): void;
    resetZoom(): void;
}

export class CanvasViewerEventHandler {
    constructor(
        private canvas: HTMLElement,
        private scrollBy: CanvasViewerEventHandlerCallbacks["scrollBy"],
        private zoomIn: CanvasViewerEventHandlerCallbacks["zoomIn"],
        private zoomOut: CanvasViewerEventHandlerCallbacks["zoomOut"],
        private zoomBy: CanvasViewerEventHandlerCallbacks["zoomBy"],
        private resetZoom: CanvasViewerEventHandlerCallbacks["resetZoom"]
    ) {
        this.canvas.addEventListener("wheel", this.onCanvasScroll, { passive: false });
        window.addEventListener("keydown", this.onKeyDown, { passive: false });
    }

    dispose() {
        this.canvas.removeEventListener("wheel", this.onCanvasScroll);
        window.removeEventListener("keydown", this.onKeyDown);
    }

    private onCanvasScroll = (event: WheelEvent) => {
        if (isCtrlKeyPressed(event)) {
            event.preventDefault();

            // Limiting for high DPI scrolling
            this.zoomBy(clamp(Math.round(-event.deltaY), -CanvasConstants.MAX_ZOOMING_SPEED, CanvasConstants.MAX_ZOOMING_SPEED), {
                x: event.clientX,
                y: event.clientY
            });
        } else {
            event.preventDefault();

            let dx = clamp(0 - event.deltaX, -CanvasConstants.MAX_SCROLLING_SPEED, CanvasConstants.MAX_SCROLLING_SPEED);
            let dy = clamp(0 - event.deltaY, -CanvasConstants.MAX_SCROLLING_SPEED, CanvasConstants.MAX_SCROLLING_SPEED);

            if (event.shiftKey) {
                // Scroll horizontally
                dx = clamp(0 - event.deltaY, -CanvasConstants.MAX_SCROLLING_SPEED, CanvasConstants.MAX_SCROLLING_SPEED);
                dy = 0;
            }

            this.scrollBy(dx, dy);
        }
    };

    private onKeyDown = (event: KeyboardEvent) => {
        if (!isCtrlKeyPressed(event)) return;

        switch (event.key) {
            case "=":
            case "+":
                event.preventDefault();
                this.zoomIn();
                break;
            case "-":
                event.preventDefault();
                this.zoomOut();
                break;
            case "0":
                event.preventDefault();
                this.resetZoom();
                break;
        }
    };
}

const isCtrlKeyPressed = (event: KeyboardEvent | WheelEvent) => {
    if (navigator.userAgentData?.platform === "macOS" || navigator?.platform === "MacIntel") {
        // Browsers send ctrlKey as true when pinch to zoom
        // https://caniuse.com/mdn-api_wheelevent_pinch_to_zoom_support
        return event.metaKey || event.ctrlKey;
    }

    return event.ctrlKey;
};
