import Microflows from "../model/gen/Microflows.gen";

import { Rectangle } from "./Rectangle";
import { assertNever } from "./assertNever";

type Point = { x: number; y: number };
type Edge = "top" | "right" | "bottom" | "left";

export function getDegreesFromXYPosition(x: number, y: number) {
    const radians = Math.atan2(y, x);
    const degrees = radians * (180 / Math.PI);

    return modulo(degrees, 360);
}

function modulo(target: number, nr: number) {
    return ((target % nr) + nr) % nr;
}

export function calculateFlowEndpoints(
    flow: Microflows.Flow,
    startRectangle: Rectangle,
    endRectangle: Rectangle
): { start: Point; end: Point } {
    const start = getConnectionPointOnRectangle(indexToEdge(flow.originConnectionIndex), startRectangle);
    const end = getConnectionPointOnRectangle(indexToEdge(flow.destinationConnectionIndex), endRectangle);

    return { start, end };
}

/**
 * Get the canvas coordinates for the midpoint of the specified edge of the given rectangle.
 *
 * @param edge "top" | "right" | "bottom" | "left"
 * @param rectangle The Rectangle
 */
function getConnectionPointOnRectangle(edge: Edge, rectangle: Rectangle): Point {
    switch (edge) {
        case "top":
            return {
                x: rectangle.middlePoint.x,
                y: rectangle.top
            };
        case "right":
            return {
                x: rectangle.right,
                y: rectangle.middlePoint.y
            };
        case "bottom":
            return {
                x: rectangle.middlePoint.x,
                y: rectangle.bottom
            };
        case "left":
            return {
                x: rectangle.left,
                y: rectangle.middlePoint.y
            };
        default:
            assertNever(edge);
    }
}

function indexToEdge(index: number): Edge {
    switch (index) {
        case 0:
            return "top";
        case 1:
            return "right";
        case 2:
            return "bottom";
        case 3:
            return "left";
    }

    throw new Error(`Unexpected connection point index for flow: ${index}.`);
}

export function getDiagonalDistanceBetweenPoints(start: Point, end: Point) {
    return Math.hypot(start.x - end.x, start.y - end.y);
}
