import { ReactElement } from "react";

import entityIconUrl from "../assets/dataTypeIcons/entity.svg";
import listIconUrl from "../assets/dataTypeIcons/list.svg";
import primitiveIconUrl from "../assets/dataTypeIcons/primitive.svg";
import DataTypes from "../model/gen/DataTypes.gen";

import { assertNever } from "./assertNever";
import { EmptyReference } from "./microflowHelpers";

export enum DataTypeKind {
    "Unknown",
    "Void",
    "Empty",
    "Binary",
    "Boolean",
    "Decimal",
    "Integer",
    "Float",
    "DateTime",
    "String",
    "Object",
    "List",
    "Enumeration"
}

export type DataTypeInfo = {
    kind: DataTypeKind;
    caption: string;
    icon: ReactElement;
};

export function getDataTypeInfo(type: DataTypes.DataType["$Type"], qualifiedName?: string | EmptyReference): DataTypeInfo {
    switch (type) {
        case "DataTypes$EnumerationType":
            return {
                kind: DataTypeKind.Enumeration,
                caption: getName(verifyQualifiedName(type, qualifiedName)),
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        case "DataTypes$ObjectType":
            return {
                kind: DataTypeKind.Object,
                caption: getName(verifyQualifiedName(type, qualifiedName)),
                icon: <img src={entityIconUrl} alt="Entity datatype" />
            };
        case "DataTypes$ListType":
            return {
                kind: DataTypeKind.List,
                caption: `List of ${getName(verifyQualifiedName(type, qualifiedName))}`,
                icon: <img src={listIconUrl} alt="List datatype" />
            };
        case "DataTypes$BinaryType":
            return {
                kind: DataTypeKind.Binary,
                caption: "Binary",
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        case "DataTypes$BooleanType":
            return {
                kind: DataTypeKind.Boolean,
                caption: "Boolean",
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        case "DataTypes$DateTimeType":
            return {
                kind: DataTypeKind.DateTime,
                caption: "DateTime",
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        case "DataTypes$DecimalType":
            return {
                kind: DataTypeKind.Decimal,
                caption: "Decimal",
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        case "DataTypes$EmptyType":
            return {
                kind: DataTypeKind.Empty,
                caption: "Empty",
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        case "DataTypes$FloatType":
            return {
                kind: DataTypeKind.Float,
                caption: "Float",
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        case "DataTypes$IntegerType":
            return {
                kind: DataTypeKind.Integer,
                caption: "Integer",
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        case "DataTypes$StringType":
            return {
                kind: DataTypeKind.String,
                caption: "String",
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        case "DataTypes$VoidType":
            return {
                kind: DataTypeKind.Void,
                caption: "Void",
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        case "DataTypes$UnknownType":
            return {
                kind: DataTypeKind.Unknown,
                caption: "(Not set)",
                icon: <img src={primitiveIconUrl} alt="Primitive datatype" />
            };
        default:
            assertNever(type);
    }
}

function verifyQualifiedName(type: string, qualifiedName: string | EmptyReference): string {
    if (!qualifiedName) {
        throw new Error(`No qualified name supplied for type: ${type}`);
    }

    return qualifiedName;
}

function getName(qualifiedName: string | null, defaultName: string = ""): string {
    return qualifiedName?.split(".").pop() ?? defaultName;
}
