type ActionData = {
    iconUrl: string;
    caption: string;
};

export function getActionData(action: Microflows.MicroflowAction | EmptyReference): ActionData {
    if (!action) return { caption: "Activity", iconUrl: DefaultIcon };

    /* prettier-ignore */
    switch (action.$Type) {
        case "Microflows$CastAction":                  return { caption: "Cast",                                    iconUrl: CastIcon };
        case "Microflows$CommitAction":                return { caption: createCommitCaption(action),               iconUrl: CommitObjectIcon };
        case "Microflows$ChangeObjectAction":          return { caption: createChangeCaption(action),               iconUrl: ChangeObjectIcon };
        case "Microflows$DeleteAction":                return { caption: createDeleteCaption(action),               iconUrl: DeleteIcon };
        case "Microflows$RetrieveAction":              return { caption: createRetrieveActionCaption(action),       iconUrl: RetrieveIcon };
        case "Microflows$AggregateListAction":         return { caption: createAggregateListCaption(action),        iconUrl: AggregateListIcon };
        case "Microflows$CreateObjectAction":          return { caption: createCreateObjectCaption(action),         iconUrl: CreateObjectIcon };
        case "Microflows$RollbackAction":              return { caption: "Rollback",                                iconUrl: RollbackIcon };
        case "Microflows$ChangeListAction":            return { caption: createChangeListCaption(action),           iconUrl: ChangeListIcon };
        case "Microflows$CreateListAction":            return { caption: createCreateListCaption(action),           iconUrl: CreateListIcon };
        case "Microflows$ListOperationAction":         return { caption: createListOperationCaption(action),        iconUrl: ListOperationIcon };
        case "Microflows$JavaActionCallAction":        return { caption: "Call Java action",                        iconUrl: JavaActionCallIcon };
        case "Microflows$MicroflowCallAction":         return { caption: createMicroflowCallCaption(action),        iconUrl: MicroflowCallIcon };
        case "Microflows$CreateVariableAction":        return { caption: createCreateVariableCaption(action),       iconUrl: CreateVariableIcon };
        case "Microflows$ChangeVariableAction":        return { caption: createChangeVariableCaption(action),       iconUrl: ChangeVariableIcon };
        case "Microflows$CloseFormAction":             return { caption: "Close page",                              iconUrl: ClosePageIcon };
        case "Microflows$DownloadFileAction":          return { caption: createDownloadFileCaption(action),         iconUrl: DownloadFileIcon };
        case "Microflows$ShowHomePageAction":          return { caption: "Show home page",                          iconUrl: ShowHomePageIcon };
        case "Microflows$ShowPageAction":              return { caption: createShowPageCaption(action),             iconUrl: ShowPageIcon };
        case "Microflows$ShowMessageAction":           return { caption: createShowMessageCaption(action),          iconUrl: getShowMessageIconUrl(action) };
        case "Microflows$PushToClientAction":          return { caption: createPushToClientCaption(action),         iconUrl: PushToClientIcon };
        case "Microflows$ValidationFeedbackAction":    return { caption: createValidationFeedbackCaption(action),   iconUrl: ValidationFeedbackIcon };
        case "Microflows$RestCallAction":              return { caption: createRestCallCaption(action),             iconUrl: RestServiceCallIcon };
        case "Microflows$WebServiceCallAction":        return { caption: "Call web service",                        iconUrl: WebServiceCallIcon };
        case "Microflows$ExportXmlAction":             return { caption: "Export",                                  iconUrl: ExportMappingIcon };
        case "Microflows$ImportXmlAction":             return { caption: "Import from JSON",                        iconUrl: ImportMappingIcon };
        case "Microflows$LogMessageAction":            return { caption: createLogMessageCaption(action),           iconUrl: LogMessageIcon };
        case "Microflows$GenerateDocumentAction":      return { caption: createGenerateDocumentCaption(action),     iconUrl: GenerateDocumentIcon };
        case "Microflows$CounterMeterAction":          return { caption: "Counter",                                 iconUrl: DefaultIcon }; // TODO: Missing icon
        case "Microflows$GaugeMeterAction":            return { caption: "Gauge",                                   iconUrl: DefaultIcon }; // TODO: Missing icon
        case "Microflows$IncrementCounterMeterAction": return { caption: "Increment counter",                       iconUrl: DefaultIcon }; // TODO: Missing icon
        case "Microflows$ApplyJumpToOptionAction":     return { caption: "Apply Jump-To Option",                    iconUrl: ApplyJumpToOptionIcon };
        case "Microflows$WorkflowCallAction":          return { caption: createWorkflowCallCaption(action),         iconUrl: WorkflowCallIcon };
        case "Microflows$WorkflowOperationAction":     return { caption: "Change workflow state",                   iconUrl: WorkflowOperationIcon };
        case "Microflows$GetWorkflowDataAction":       return { caption: "Retrieve workflow context",               iconUrl: GetWorkflowDataIcon };
        case "Microflows$GenerateJumpToOptionsAction": return { caption: "Generate Jump-To Options",                iconUrl: GenerateJumpToOptionsIcon };
        case "Microflows$OpenWorkflowAction":          return { caption: "Show workflow admin page",                iconUrl: OpenWorkflowIcon };
        case "Microflows$OpenUserTaskAction":          return { caption: "Show user task page",                     iconUrl: OpenUserTaskIcon };
        case "Microflows$SetTaskOutcomeAction":        return { caption: "Complete task",                           iconUrl: CompleteTaskIcon };
        case "Microflows$DeleteExternalObject":        return { caption: createDeleteExternalObjectCaption(action), iconUrl: DeleteIcon };
        case "Microflows$SendExternalObject":          return { caption: createSendExternalObjectCaption(action),   iconUrl: SendExternalObjectIcon };
        default:
            // The type checker doesn't know about any other types so assumes
            // that this case is unreachable. To make sure that we don't break
            // when new action types are introduced, the action is cast to `any`.
            return {
                // eslint-disable-next-line @typescript-eslint/no-explicit-any
                caption: (action as any).$Type ?? "Activity",
                iconUrl: ""
            };
    }
}

function createAggregateListCaption(action: Microflows.AggregateListAction): string {
    if (action.aggregateFunction === "Count") {
        return action.inputListVariableName ? `Count ${action.inputListVariableName}` : "Count";
    }

    const functionName = action.aggregateFunction.toLowerCase();

    if (action.inputListVariableName && action.attribute) {
        return `Take ${functionName} of attribute '${action.attribute}' in ${action.inputListVariableName}`;
    }

    return `Take ${functionName}`;
}

function createChangeListCaption(action: Microflows.ChangeListAction): string {
    let caption: string;

    switch (action.type) {
        case "Add":
            caption = "Add to list";
            break;
        case "Remove":
            caption = "Remove from list";
            break;
        case "Clear":
            caption = "Clear list";
            break;
        case "Set":
            caption = "Set list";
            break;
    }

    if (action.changeVariableName) {
        return `${caption} ${action.changeVariableName}`;
    }

    return caption;
}

function createCreateListCaption(action: Microflows.CreateListAction): string {
    return action.entity ? `Create list of ${action.entity}` : "Create list";
}

function createCreateVariableCaption(action: Microflows.CreateVariableAction): string {
    return action.variableType ? `Create ${datatypeToString(action.variableType)} variable` : "Create variable";
}

function createChangeVariableCaption(action: Microflows.ChangeVariableAction): string {
    return action.changeVariableName ? `Change variable ${action.changeVariableName}` : "Change variable";
}

function createListOperationCaption(action: Microflows.ListOperationAction): string {
    /* prettier-ignore */
    switch (action.operation.$Type) {
        case "Microflows$Intersect":          return "Intersect";
        case "Microflows$Union":              return "Union";
        case "Microflows$Subtract":           return "Subtract";
        case "Microflows$Contains":           return "Contains";
        case "Microflows$ListEquals":         return "Equals";
        case "Microflows$Sort":               return "Sort";
        case "Microflows$Filter":             return "Filter";
        case "Microflows$FilterByExpression": return "Filter by expression";
        case "Microflows$Find":               return "Find";
        case "Microflows$FindByExpression":   return "Find by expression";
        case "Microflows$Head":               return "Head";
        case "Microflows$Tail":               return "Tail";
        default:                              return "List operation";
    }
}

function createRetrieveActionCaption(action: Microflows.RetrieveAction): string {
    if (action.retrieveSource!.$Type === "Microflows$DatabaseRetrieveSource") {
        if (!action.retrieveSource?.entity) {
            return `Retrieve from database`;
        }

        const name = action.retrieveSource!.entity.split(".").pop();

        return `Retrieve '${name}' from database`;
    } else {
        return "Retrieve from association";
    }
}

// TODO: Trim microflow name if it is in the same module
function createMicroflowCallCaption(action: Microflows.MicroflowCallAction): string {
    return action.microflowCall.microflow ? action.microflowCall.microflow : "Call microflow";
}

function datatypeToString(type: DataTypes.DataType): string {
    switch (type.$Type) {
        case "DataTypes$EnumerationType":
            return `Enumeration ${type.enumeration}`;
        case "DataTypes$DateTimeType":
            return "Date and time";
        default:
            return type.$Type.split("$")[1].replace(/Type$/, "");
    }
}

function createDownloadFileCaption(action: Microflows.DownloadFileAction): string {
    return action.fileDocumentVariableName ? `Download file ${action.fileDocumentVariableName}` : "Download file";
}

function createShowPageCaption(action: Microflows.ShowPageAction): string {
    return action.pageSettings.page ? `Show page '${action.pageSettings.page}'` : "Show page";
}

function createShowMessageCaption(action: Microflows.ShowMessageAction): string {
    const translations = action.template.text.translations;

    if (translations.length === 0) {
        return "";
    }

    // TODO: Return the en_US one or some other default
    return translations[0].text;
}

function getShowMessageIconUrl(action: Microflows.ShowMessageAction): string {
    switch (action.type) {
        case "Information":
            return ShowInfoMessageIcon;
        case "Warning":
            return ShowWarningMessageIcon;
        case "Error":
            return ShowErrorMessageIcon;
        default:
            assertNever(action.type);
    }
}

function createPushToClientCaption(action: Microflows.PushToClientAction): string {
    return action.dataVariableName ? `Synchronize '${action.dataVariableName}' to device` : "Synchronize to device";
}

function createValidationFeedbackCaption(action: Microflows.ValidationFeedbackAction): string {
    if (action.objectVariableName) {
        return `Show validation message on member '${action.attribute}' of ${action.objectVariableName}`;
    }

    if (action.association) {
        return `Show validation message on member '${action.association}' of ${action.objectVariableName}`;
    }

    return "Validate feedback";
}

function createRestCallCaption(action: Microflows.RestCallAction): string {
    const method = action.httpConfiguration.newHttpMethod.toUpperCase();

    return `Call REST (${method})`;
}

function createLogMessageCaption(action: Microflows.LogMessageAction): string {
    const level = action.level.toLowerCase();

    return `Log message (${level})`;
}

function createGenerateDocumentCaption(action: Microflows.GenerateDocumentAction): string {
    let documentType: string;

    switch (action.documentType) {
        case "HTML":
            documentType = "HTML (.html)";
            break;
        case "PDF":
            documentType = "PDF (.pdf)";
            break;
        case "DOCX":
            documentType = "Word 2007 (.docx)";
            break;
        case "DOC":
            documentType = "Word 2003 (.doc)";
            break;
        case "RTF":
            documentType = "Rich-text format (.rtf)";
            break;
        case "ODT":
            documentType = "Open Office (.odt)";
            break;
        default:
            assertNever(action.documentType);
    }

    return `Generate ${documentType} document`;
}

// TODO: Remove module name if it's in the same module.
function createWorkflowCallCaption(action: Microflows.WorkflowCallAction): string {
    return action.workflow ? `Call '${action.workflow}'` : "Call workflow";
}

function createCreateObjectCaption(action: Microflows.CreateObjectAction): string {
    return action.entity ? `Create ${action.entity}` : "Create";
}

function createDeleteCaption(action: Microflows.DeleteAction): string {
    return action.deleteVariableName ? `Delete '${action.deleteVariableName}'` : "Delete";
}

function createChangeCaption(action: Microflows.ChangeObjectAction): string {
    return action.changeVariableName ? `Change '${action.changeVariableName}'` : "Change";
}

function createCommitCaption(action: Microflows.CommitAction): string {
    return action.commitVariableName ? `Commit '${action.commitVariableName}'` : "Commit";
}

function createSendExternalObjectCaption(action: Microflows.SendExternalObject): string {
    return action.variableNameToBeSent ? `Send '${action.variableNameToBeSent}'` : "Send";
}

function createDeleteExternalObjectCaption(action: Microflows.DeleteExternalObject): string {
    return action.deleteVariableName ? `Delete '${action.deleteVariableName}'` : "Delete";
}
