import { observer } from "mobx-react";
import { FC } from "react";

import { getDataTypeInfo } from "../../helpers/dataTypeHelpers";
import { getPositionStyleForMicroflowObject } from "../../helpers/microflowHelpers";
import Microflows from "../../model/gen/Microflows.gen";
import { ParameterShape } from "../shapes/ParameterShape";

import { MicroflowObject } from "./MicroflowObject";

export const Parameter: FC<{ microflowObject: Microflows.MicroflowParameterObject }> = observer(({ microflowObject }) => {
    const style = getPositionStyleForMicroflowObject(microflowObject);

    const dataType = microflowObject.variableType;

    const qualifiedName =
        dataType.$Type === "DataTypes$EnumerationType"
            ? dataType.enumeration
            : dataType.$Type === "DataTypes$ObjectType" || dataType.$Type === "DataTypes$ListType"
            ? dataType.entity
            : undefined;

    const outputVariable = {
        name: microflowObject.name,
        type: getDataTypeInfo(dataType.$Type, qualifiedName)
    };

    return (
        <MicroflowObject {...style} data-testid="parameter-object">
            <ParameterShape outputVariable={outputVariable} />
        </MicroflowObject>
    );
});
