import { observer } from "mobx-react";
import { CSSProperties, FC, useContext } from "react";
import styled from "styled-components";

import breakpointIcon from "../assets/breakpointIcons/Breakpoint.svg";
import skipBreakpointIcon from "../assets/breakpointIcons/SkipBreakpoint.svg";
import Microflows from "../model/gen/Microflows.gen";
import { StateContext } from "../state/AppState";

type Position = Pick<CSSProperties, "left" | "top" | "right" | "bottom">;

type BreakpointProps = {
    microflowObject: Microflows.MicroflowObject;
    position: Position;
};

export const Breakpoint: FC<BreakpointProps> = observer(({ microflowObject, position }) => {
    const state = useContext(StateContext);

    const breakpoint = state.breakpoints.find(breakpoint => breakpoint.ContainerID === microflowObject.$ID);

    if (!breakpoint) return null;

    return <BreakpointIcon src={breakpoint.Enabled ? breakpointIcon : skipBreakpointIcon} style={position} />;
});

const BreakpointIcon = styled.img`
    position: absolute;
    width: 14px;
    height: 14px;
`;
