import { Point, Rectangle } from "../helpers/Rectangle";
import { getRectangle } from "../helpers/microflowHelpers";
import { calculateFlowEndpoints } from "../helpers/rectangleHelpers";

import $Primitives from "./gen/$Primitives.gen";
import Microflows from "./gen/Microflows.gen";

/**
 * A Bézier curve with start and end points and two control points.
 *
 * @see https://en.wikipedia.org/wiki/B%C3%A9zier_curve
 * @see https://pomax.github.io/bezierinfo/
 */
export class BezierCurve {
    /**
     * Returns a new instance based on the given model objects.
     *
     * In the model, the control points are stored as vectors, relative to
     * origin and destination (`originBezierVector` resp.
     * `destinationBezierVector`). Having all points share the same coordinate
     * space makes the calculations in this class a lot simpler.
     *
     * For easier of the class, the actual constructor is kept plain and the
     * model logic is handled in this function.
     *
     * @returns A new instance based on the given model objects.
     */
    static fromModel(flow: Microflows.Flow, origin: Microflows.MicroflowObject, destination: Microflows.MicroflowObject): BezierCurve {
        const { start, end } = calculateFlowEndpoints(flow, getRectangle(origin), getRectangle(destination));

        return new BezierCurve(
            start,
            this.addPointAndVector(start, flow.originBezierVector),
            this.addPointAndVector(end, flow.destinationBezierVector),
            end
        );
    }

    /**
     * @param start - The start point of the curve
     * @param startControl - The first control point. Absolutely positioned.
     * @param endControl - The second control point. Absolutely positioned.
     * @param end - The end point of the curve
     */
    constructor(readonly start: Point, readonly startControl: Point, readonly endControl: Point, readonly end: Point) {}

    /**
     * Returns the Point on the canvas corresponding to `t`.
     */
    coordinatesFor(t: number): Point {
        return {
            x: this.interpolate(t, "x"),
            y: this.interpolate(t, "y")
        };
    }

    /**
     * Interpolates the given coordinate according to this curve and value `t`.
     *
     * @returns The value for the given @coordinate for the given `t`.
     */
    private interpolate(t: number, coordinate: keyof Point): number {
        return (
            (1 - t) ** 3 * this.start[coordinate] +
            (1 - t) ** 2 * t * 3 * this.startControl[coordinate] +
            (1 - t) * t ** 2 * 3 * this.endControl[coordinate] +
            t ** 3 * this.end[coordinate]
        );
    }

    calculateBoundingBox(): Rectangle {
        const left = this.smallest("x");
        const right = this.biggest("x");

        const top = this.smallest("y");
        const bottom = this.biggest("y");

        return new Rectangle(left, top, right - left, bottom - top);
    }

    /**
     * Returns an SVG path description that can be used in a <path d="..." /> property.
     */
    toSvg(): string {
        const cPoint1 = `${this.startControl.x},${this.startControl.y}`;
        const cPoint2 = `${this.endControl.x},${this.endControl.y}`;

        return `M${this.start.x},${this.start.y} C${cPoint1} ${cPoint2} ${this.end.x},${this.end.y}`;
    }

    private static addPointAndVector(point: Point, vector: $Primitives.Size): Point {
        return {
            x: point.x + vector.width,
            y: point.y + vector.height
        };
    }

    private smallest(field: keyof Point): number {
        return Math.min(this.start[field], this.end[field], this.startControl[field], this.endControl[field]);
    }

    private biggest(field: keyof Point): number {
        return Math.max(this.start[field], this.end[field], this.startControl[field], this.endControl[field]);
    }
}
