export class Rectangle {
    static createFromXYPositions(corner1: Point, corner2: Point) {
        const left = Math.min(corner1.x, corner2.x);
        const right = Math.max(corner1.x, corner2.x);
        const top = Math.min(corner1.y, corner2.y);
        const bottom = Math.max(corner1.y, corner2.y);

        return new Rectangle(left, top, right - left, bottom - top);
    }

    constructor(
        private readonly _left: number = 0,
        private readonly _top: number = 0,
        private readonly _width: number = 0,
        private readonly _height: number = 0
    ) {}

    get left() {
        return this._left;
    }

    get top() {
        return this._top;
    }

    get right() {
        return this.left + this.width;
    }

    get bottom() {
        return this.top + this.height;
    }

    get width() {
        return this._width;
    }

    get height() {
        return this._height;
    }

    get middlePoint(): Point {
        return {
            x: this.left + this.width / 2,
            y: this.top + this.height / 2
        };
    }

    expand(_amount: number): Rectangle;
    expand(_width: number, _height: number): Rectangle;
    expand(width: number, optionalHeight?: number): Rectangle {
        const height = optionalHeight === undefined || !isFinite(optionalHeight) ? width : optionalHeight;

        return new Rectangle(this.left - width, this.top - height, this.width + width * 2, this.height + height * 2);
    }

    /**
     * Returns a new Rectangle that is @amount pixels bigger in width and height than this one
     * while retaining the same middle point.
     */
    expandFromMiddlePoint(amount: number) {
        const halfAmount = amount / 2;

        return new Rectangle(this.left - halfAmount, this.top - halfAmount, this.width + amount, this.height + amount);
    }

    expandByAmounts(left: number, top: number, right: number, bottom: number): Rectangle {
        return new Rectangle(this.left - left, this.top - top, this.width + left + right, this.height + top + bottom);
    }

    intersectsWith(rect: Rectangle): boolean {
        return rect.left < this.right && this.left < rect.right && rect.top < this.bottom && this.top < rect.bottom;
    }

    intersect(rect: Rectangle): Rectangle {
        const maxLeft = Math.max(rect.left, this.left);
        const minRight = Math.min(rect.right, this.right);
        const maxTop = Math.max(rect.top, this.top);
        const minBottom = Math.min(rect.bottom, this.bottom);

        const width = minRight - maxLeft;
        const height = minBottom - maxTop;

        if (width > 0 && height > 0) {
            return new Rectangle(maxLeft, maxTop, width, height);
        }

        return new Rectangle();
    }

    relativeToTopLeft(point: Point): Point {
        return {
            x: point.x - this.left,
            y: point.y - this.top
        };
    }

    translate(delta: Point): Rectangle {
        return new Rectangle(this.left + delta.x, this.top + delta.y, this.width, this.height);
    }

    union(rect: Rectangle): Rectangle {
        const minX = Math.min(this.left, rect.left);
        const minY = Math.min(this.top, rect.top);
        const width = Math.max(this.right, rect.right) - minX;
        const height = Math.max(this.bottom, rect.bottom) - minY;

        return new Rectangle(minX, minY, width, height);
    }

    isCompletelyInside(rect: Rectangle) {
        return !this.isPartiallyOutside(rect);
    }

    isPartiallyOutside(rect: Rectangle) {
        return this.left < rect.left || this.right > rect.right || this.top < rect.top || this.bottom > rect.bottom;
    }

    containsPoint(point: Point): boolean {
        return this.left <= point.x && point.x <= this.right && this.top <= point.y && point.y <= this.bottom;
    }

    findClosestPointOnBorder(point: Point): Point {
        let { x, y } = point;

        x = clamp(x, this.left, this.right);
        y = clamp(y, this.top, this.bottom);

        const distanceLeft = Math.abs(x - this.left);
        const distanceRight = Math.abs(x - this.right);
        const distanceTop = Math.abs(y - this.top);
        const distanceBottom = Math.abs(y - this.bottom);

        const minimumDistance = Math.min(distanceLeft, distanceRight, distanceTop, distanceBottom);

        switch (minimumDistance) {
            case distanceTop:
                return { x: x, y: this.top };
            case distanceBottom:
                return { x: x, y: this.bottom };
            case distanceLeft:
                return { x: this.left, y: y };
            case distanceRight:
                return { x: this.right, y: y };
            default:
                throw new Error("Closest point calculation failed!");
        }
    }

    getAllReferencePoints(): Point[] {
        return [
            this.createPoint(this.left, this.top),
            this.createPoint(this.left, this.middlePoint.y),
            this.createPoint(this.left, this.bottom),
            this.createPoint(this.right, this.top),
            this.createPoint(this.right, this.middlePoint.y),
            this.createPoint(this.right, this.bottom),
            this.createPoint(this.middlePoint.x, this.top),
            this.createPoint(this.middlePoint.x, this.middlePoint.y),
            this.createPoint(this.middlePoint.x, this.bottom)
        ];
    }

    move(left: number, top: number) {
        return new Rectangle(this.left + left, this.top + top, this.width, this.height);
    }

    private createPoint(x: number, y: number): Point {
        return {
            x: x,
            y: y
        };
    }
}

export type Point = {
    x: number;
    y: number;
};

function clamp(target: number, min: number, max: number) {
    return target < min ? min : target > max ? max : target;
}
