import { observer } from "mobx-react";
import { ChangeEventHandler, FC, useCallback, useContext } from "react";
import styled from "styled-components";

import { CanvasConstants } from "../helpers/CanvasConstants";
import { generateMockBreakpoints, generateMockProblems } from "../helpers/generateMockData";
import { StateContext } from "../state/AppState";

import { DocumentSelector } from "./DocumentSelector";
import { Dropdown } from "./Dropdown";

type ZoomLevelOption = { level: number; disabled: boolean };

export const TopBar: FC = observer(() => {
    const state = useContext(StateContext);

    const onZoomLevelChange: ChangeEventHandler<HTMLSelectElement> = useCallback(
        event => {
            if (!event.currentTarget.value) return;

            const zoomLevel = Number(event.currentTarget.value);

            state.canvasViewManager.zoomTo(zoomLevel);
        },
        [state]
    );

    const zoomLevels: Array<ZoomLevelOption> = CanvasConstants.ZOOM_LEVEL_OPTIONS.map(level => ({
        level,
        disabled: false
    }));

    const isCustomZoomLevel = !CanvasConstants.ZOOM_LEVEL_OPTIONS.includes(state.canvasViewManager.zoomLevel);

    if (isCustomZoomLevel) {
        zoomLevels.unshift({ level: state.canvasViewManager.zoomLevel, disabled: true });
    }

    const dropdownItems = [
        {
            content: "Add Mock Problems",
            action: () => generateMockProblems(state)
        },
        {
            content: "Add Mock Breakpoints",
            action: () => generateMockBreakpoints(state)
        }
    ];

    // Add an empty <div> if there's no DocumentSelector so the Zoom level dropdown stays at the right.
    return (
        <Container data-testid="top-bar">
            <>
                {state.isDevMode ? (
                    <div>
                        <DocumentSelector />
                        <Dropdown actions={dropdownItems} buttonText="Actions" />
                    </div>
                ) : (
                    <div />
                )}

                {state.openDocument ? (
                    <div>
                        <label htmlFor="zoom-level">Zoom Level</label>
                        <select value={state.canvasViewManager.zoomLevel} onChange={onZoomLevelChange} id="zoom-level">
                            {zoomLevels.map(({ level, disabled }) => (
                                <option value={level} key={`option-${level}`} disabled={disabled}>
                                    {level}%
                                </option>
                            ))}
                        </select>
                    </div>
                ) : null}
            </>
        </Container>
    );
});

const Container = styled.header`
    border-bottom: 1px solid gray;
    background-color: #e7e7e9;
    padding: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
`;
