import { CSSProperties } from "react";
import styled from "styled-components";

export type RectanglePositionStyle = Pick<CSSProperties, "top" | "left" | "width" | "height">;

export const MicroflowObject = styled.div.attrs<RectanglePositionStyle>(props => ({
    style: {
        top: props.top,
        left: props.left,
        width: props.width,
        height: props.height
    }
}))`
    position: absolute;
    word-break: break-word;
    font-size: 10px;
`;
