import { CSSProperties, FC, useMemo } from "react";
import styled from "styled-components";

import { DataTypeInfo, DataTypeKind } from "../../helpers/dataTypeHelpers";

export type VariableOutputDescriptionProps = {
    name: string;
    type: DataTypeInfo;
};

export const VariableOutputDescription: FC<VariableOutputDescriptionProps> = ({ name, type }) => {
    const variableTypeMargin = useMemo<CSSProperties>(() => ({ marginTop: name ? "5px" : 0 }), [name]);

    return (
        <StyledOutputDescription data-testid={`variable-output-description-${name ? name : "empty"}`}>
            {name ? <VariableName title={name}>{name}</VariableName> : null}
            <VariableType style={variableTypeMargin}>
                {type.kind !== DataTypeKind.Unknown ? <VariableTypeIcon>{type.icon}</VariableTypeIcon> : null}
                <VariableTypeText title={type.caption}>{type.caption}</VariableTypeText>
            </VariableType>
        </StyledOutputDescription>
    );
};

const StyledOutputDescription = styled.div`
    position: absolute;
    left: 50%;
    max-width: 100px;
    margin-top: 7px;
    cursor: pointer;
    transform: translate(-50%, 0);
    text-align: center;
    pointer-events: none;
`;

const VariableName = styled.div`
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    pointer-events: auto;
    color: #0a1324;
`;

const VariableType = styled.div`
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    justify-content: center;
    white-space: nowrap;
    color: #298efb;
    font-weight: 600;
`;

const VariableTypeIcon = styled.div`
    width: 14px;
    height: 14px;
    margin-right: 5px;
`;

const VariableTypeText = styled.span`
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    pointer-events: auto;
`;
