import { FC, PropsWithChildren } from "react";
import styled from "styled-components";

import { calculateFlowArrowOrientation } from "../../../helpers/calculateFlowArrowOrientation";
import { BezierCurve } from "../../../model/BezierCurve";

type Props = {
    curve: BezierCurve;
    objectId: string;
};

const ARROW_WIDTH = 12;
const ARROW_LENGTH = ARROW_WIDTH;

export const FlowCurve: FC<PropsWithChildren<Props>> = ({ curve, objectId, children }) => {
    // Expand the bounding box to make sure that the Arrow also fits
    const svgRectangle = curve.calculateBoundingBox().expandFromMiddlePoint(ARROW_WIDTH);

    /*
     * The `auto` orientation just uses the orientation of the curve at t=1, which
     * works for most cases, but not when the control point is close to the endpoint
     * and the angle is big.
     *
     * If performance turns out to be a problem, we can likely only perform this calculation
     * for difficult cases (large angles, close control point distances) and draw the rest
     * with `<marker orient="auto" />`.
     */
    const arrowOrientation = calculateFlowArrowOrientation(curve, ARROW_LENGTH);

    const uniqueId = `arrowHead_${objectId}`;

    /** An arrow pointing to the right, with the tip at { x: ARROW_LENGTH, y: ARROW_WIDTH / 2 } */
    const arrowHeadPath = `M 0,0 L ${ARROW_LENGTH},${ARROW_WIDTH / 2} L 0,${ARROW_WIDTH} Z`;

    return (
        <svg
            style={{
                position: "absolute",
                top: svgRectangle.top,
                left: svgRectangle.left,
                width: svgRectangle.width,
                height: svgRectangle.height
            }}
            width={svgRectangle.width}
            height={svgRectangle.height}
            viewBox={`${svgRectangle.left} ${svgRectangle.top} ${svgRectangle.width} ${svgRectangle.height}`}
        >
            <Path d={curve.toSvg()} markerEnd={`url(#${uniqueId})`} />
            <defs>
                <marker
                    id={uniqueId}
                    refX={ARROW_LENGTH}
                    refY={ARROW_WIDTH / 2}
                    markerWidth={ARROW_LENGTH}
                    markerHeight={ARROW_WIDTH}
                    orient={arrowOrientation}
                >
                    <path d={arrowHeadPath} />
                </marker>
            </defs>
            {children}
        </svg>
    );
};

const Path = styled.path`
    fill: none;
    stroke: black;
    stroke-width: 1px;
`;
