import { FC } from "react";
import styled from "styled-components";

import { Point } from "../../../helpers/Rectangle";
import $Primitives from "../../../model/gen/$Primitives.gen";

export const BezierHandle: FC<{ point: Point; size: $Primitives.Size }> = ({ point, size }) => {
    const handleDiameter = 8;

    const d = `M${point.x},${point.y} ${point.x + size.width},${point.y + size.height}`;

    return (
        <>
            <BezierHandleLine d={d} />
            <BezierControlPoint
                x={point.x + size.width - handleDiameter / 2}
                y={point.y + size.height - handleDiameter / 2}
                width={handleDiameter}
                height={handleDiameter}
            />
        </>
    );
};

const BezierHandleLine = styled.path`
    fill: none;
    stroke: black;
    stroke: rgb(16, 16, 48);
    stroke-width: 1px;
`;

const BezierControlPoint = styled.rect`
    stroke: rgb(16, 16, 48);
    stroke-width: 1px;
    fill: none;
`;
