import { FC, useContext } from "react";
import styled from "styled-components";

import { Rectangle } from "../../../helpers/Rectangle";
import { getRectangle } from "../../../helpers/microflowHelpers";
import { calculateFlowEndpoints } from "../../../helpers/rectangleHelpers";
import Microflows from "../../../model/gen/Microflows.gen";
import { StateContext } from "../../../state/AppState";

export const AnnotationFlow: FC<{ flow: Microflows.AnnotationFlow }> = ({ flow }) => {
    const state = useContext(StateContext);

    const origin = state.microflowObjectMap?.get(flow.origin);
    const destination = state.microflowObjectMap?.get(flow.destination);

    if (!origin || !destination) return null;

    const { start, end } = calculateFlowEndpoints(flow, getRectangle(origin.microflowObject), getRectangle(destination.microflowObject));

    const svgRectangle = Rectangle.createFromXYPositions(start, end);

    return (
        <svg
            style={{
                position: "absolute",
                top: svgRectangle.top,
                left: svgRectangle.left,
                width: svgRectangle.width,
                height: svgRectangle.height
            }}
            width={svgRectangle.width}
            height={svgRectangle.height}
            viewBox={`${svgRectangle.left} ${svgRectangle.top} ${svgRectangle.width} ${svgRectangle.height}`}
        >
            <Path d={`M${start.x},${start.y} ${end.x},${end.y}`} />
        </svg>
    );
};

const Path = styled.path`
    fill: none;
    stroke: black;
    stroke-width: 1px;
    stroke-dasharray: 4, 4;
`;
