import { IState } from "../state/IState";

import { MicroflowObjectPositions } from "./microflowHelpers";

export type CanvasBounds = Record<"x" | "y" | "width" | "height", number>;

export function getCanvasBounds(state: IState, microflowObjectPositions: MicroflowObjectPositions): CanvasBounds | null {
    if (!state.openDocument) return null;

    // The canvas is physically as big as needed to display all the elements, though always minimally starting at 0,0.
    // When the viewport is moved away from the physical canvas, however, the canvas size increases to correctly show
    // scrollbars and the like.

    const { leftPositions, topPositions, rightPositions, bottomPositions } = microflowObjectPositions;

    const minX = Math.min(0, state.canvasViewManager.canvasViewportRect.x, ...leftPositions);

    const minY = Math.min(0, state.canvasViewManager.canvasViewportRect.y, ...topPositions);

    const maxX = Math.max(
        state.canvasViewManager.canvasViewportRect.x + state.canvasViewManager.canvasViewportRect.width,
        ...rightPositions
    );

    const maxY = Math.max(
        state.canvasViewManager.canvasViewportRect.y + state.canvasViewManager.canvasViewportRect.height,
        ...bottomPositions
    );

    return {
        x: minX,
        y: minY,
        width: maxX - minX,
        height: maxY - minY
    };
}
