import { observer } from "mobx-react";
import { FC, useContext } from "react";
import styled from "styled-components";

import { getRectangle } from "../helpers/microflowHelpers";
import Microflows from "../model/gen/Microflows.gen";
import { StateContext } from "../state/AppState";

const BADGE_SIZE = 20;

export const ActiveProblemsBadges: FC<{ microflowObject: Microflows.MicroflowObject }> = observer(({ microflowObject }) => {
    const state = useContext(StateContext);

    const errorsCount = state.errorCounts[microflowObject.$ID];
    const warningsCount = state.warningCounts[microflowObject.$ID];

    if (!errorsCount && !warningsCount) return null;

    const microflowObjectPosition = getRectangle(microflowObject);

    const badgeWrapperStyle = {
        minWidth: microflowObjectPosition.width,
        top: microflowObjectPosition.top - BADGE_SIZE / 2,
        left: microflowObjectPosition.left + microflowObjectPosition.width + BADGE_SIZE / 2
    };

    if (
        microflowObject.$Type === "Microflows$InheritanceSplit" ||
        microflowObject.$Type === "Microflows$ExclusiveSplit" ||
        microflowObject.$Type === "Microflows$ExclusiveMerge"
    ) {
        badgeWrapperStyle.top = microflowObjectPosition.top + microflowObjectPosition.height * 0.25 - BADGE_SIZE / 2;
        badgeWrapperStyle.left = microflowObjectPosition.left + microflowObjectPosition.width * 0.75 + BADGE_SIZE / 2;
    }

    return (
        <BadgesWrapper style={badgeWrapperStyle}>
            {errorsCount ? <ErrorBadge>{errorsCount}</ErrorBadge> : null}
            {warningsCount ? <WarningBadge>{warningsCount}</WarningBadge> : null}
        </BadgesWrapper>
    );
});

const BadgesWrapper = styled.div`
    --badge-size: ${BADGE_SIZE}px;

    text-align: right;
    position: absolute;
    white-space: nowrap;
    transform: translateX(-100%);
`;

const Badge = styled.div`
    width: var(--badge-size);
    height: var(--badge-size);
    border-radius: 50%;
    line-height: var(--badge-size);
    text-align: center;
    display: inline-block;
    font-size: 11px;

    & + & {
        margin-left: 9px;
    }
`;

const ErrorBadge = styled(Badge)`
    background-color: #fb4a4c;
    color: white;
    box-shadow: 0 0 1px 3px rgba(251, 74, 76, 0.4);
`;

const WarningBadge = styled(Badge)`
    background-color: orange;
    color: white;
    box-shadow: 0 0 1px 3px rgba(251, 74, 76, 0.4);
`;
