import { FC } from "react";
import styled from "styled-components";

import errorIconUrl from "../../assets/eventIcons/error.svg";
import { assertNever } from "../../helpers/assertNever";
import { getPositionStyleForMicroflowObject, MicroflowEventType } from "../../helpers/microflowHelpers";
import { Breakpoint } from "../Breakpoint";

import { MicroflowObject } from "./MicroflowObject";

// The diameter is hard-coded in the legacy Studio Pro implementation:
// It cannot be resized in the UI.
const DIAMETER = 20;
const ICON_WIDTH = 9;

export const MicroflowEvent: FC<{ microflowObject: MicroflowEventType }> = ({ microflowObject }) => {
    const type = microflowObject.$Type;
    const style = { ...getPositionStyleForMicroflowObject(microflowObject), border: "1px solid black" };

    const { borderColor, fillColor } = colorsForType(type);

    const hasCircle = type === "Microflows$ContinueEvent" || type === "Microflows$BreakEvent";
    const icon = type === "Microflows$ErrorEvent" ? errorIconUrl : undefined;

    const objectRadius = DIAMETER / 2;
    const strokeWidth = 2;

    const outerCircleRadius = objectRadius - strokeWidth / 2;
    const innerCircleRadius = ICON_WIDTH / 2;

    return (
        <MicroflowObject {...style}>
            <svg width={DIAMETER} height={DIAMETER}>
                <circle
                    cx={objectRadius}
                    cy={objectRadius}
                    r={outerCircleRadius}
                    strokeWidth={strokeWidth}
                    stroke={borderColor}
                    fill={fillColor}
                />
                {hasCircle && (
                    <circle
                        cx={objectRadius}
                        cy={objectRadius}
                        r={innerCircleRadius}
                        strokeWidth={strokeWidth}
                        stroke={borderColor}
                        fill="transparent"
                    />
                )}
            </svg>
            {icon && (
                <EventIcon>
                    <img src={icon} />
                </EventIcon>
            )}
            <Breakpoint microflowObject={microflowObject} position={{ right: -4, top: -4 }} />
        </MicroflowObject>
    );
};

function colorsForType(type: MicroflowEventType["$Type"]): { borderColor: string; fillColor: string } {
    switch (type) {
        case "Microflows$BreakEvent":
            return { borderColor: "#f5a623", fillColor: "#f2cd91" };
        case "Microflows$ContinueEvent":
            return { borderColor: "#888888", fillColor: "#bebebe" };
        case "Microflows$ErrorEvent":
            return { borderColor: "#fccd8e", fillColor: "#fef0dd" };
        case "Microflows$EndEvent":
            return { borderColor: "#ed1c24", fillColor: "#f68d91" };
        case "Microflows$StartEvent":
            return { borderColor: "#78ca02", fillColor: "#bbe480" };
        default:
            assertNever(type);
    }
}

const EventIcon = styled.div`
    position: absolute;
    top: ${(DIAMETER - ICON_WIDTH) / 2}px;
    left: ${(DIAMETER - ICON_WIDTH) / 2}px;
    width: ${ICON_WIDTH}px;
    height: ${ICON_WIDTH}px;
    padding: 0;
`;
