import { FC } from "react";
import styled from "styled-components";

import { getPositionStyleForMicroflowObject } from "../../helpers/microflowHelpers";
import Microflows from "../../model/gen/Microflows.gen";
import { Breakpoint } from "../Breakpoint";

import { MicroflowObject } from "./MicroflowObject";
import { getActionData } from "./actions/getActionData";

export const ActionActivity: FC<{ microflowObject: Microflows.ActionActivity }> = ({ microflowObject }) => {
    const style = getPositionStyleForMicroflowObject(microflowObject);

    const action = getActionData(microflowObject.action);

    const caption = microflowObject.autoGenerateCaption ? action.caption : microflowObject.caption;

    return (
        <StyledActionActivity {...style}>
            <ActionIcon src={action.iconUrl} />
            {
                <Caption>
                    <CaptionText>{caption}</CaptionText>
                </Caption>
            }
            <Breakpoint microflowObject={microflowObject} position={{ right: 2, bottom: 2 }} />
        </StyledActionActivity>
    );
};

const StyledActionActivity = styled(MicroflowObject)`
    display: flex;
    flex-direction: row;
    justify-content: center;

    padding: 3px;

    background-color: #dceefe;
    border: solid 2px #6ebdff;
    border-radius: 10px;

    overflow: hidden;
`;

const ActionIcon = styled.img`
    align-self: center;

    width: 20px;
    height: 20px;

    margin-right: 3px;
`;

const Caption = styled.div`
    display: flex;
    flex-direction: column;

    height: 100%;
    justify-content: center;
`;

/**
 * CaptionText exists to make the text be vertically centered if it fits inside the
 * activity, and otherwise keep the top of the caption visible (and clips the bottom).
 */
const CaptionText = styled.span`
    max-height: 100%;
`;
