import { FC } from "react";

import { getPositionStyleForMicroflowObject } from "../../helpers/microflowHelpers";
import Microflows from "../../model/gen/Microflows.gen";
import { Breakpoint } from "../Breakpoint";

import { MicroflowObject } from "./MicroflowObject";

export const InheritanceSplit: FC<{ microflowObject: Microflows.InheritanceSplit }> = ({ microflowObject }) => {
    const style = getPositionStyleForMicroflowObject(microflowObject);

    return (
        <MicroflowObject {...style}>
            <svg>
                <polygon points="1,30 45,59 89,30 45,1" strokeWidth="2" fill="#e1fae1" stroke="#92ee92" />
            </svg>
            <Breakpoint
                microflowObject={microflowObject}
                position={{ right: `calc(${style.width} / 4 - 6px)`, top: `calc(${style.height} / 4 - 6px)` }}
            />
        </MicroflowObject>
    );
};
